
@import "helpers/all";
@import "generated/sprite-svg";
@import "common";
@import "layouts";
@import "main";

* {
	font-family: 'Open Sans', sans-serif; }
.wrap {
	width: 50%;
	margin: 0 auto;
	border: 1px solid #bbb;
	margin-bottom: 40px;
	@include r(900) {
		width: 100%;
		border: none;
		margin-bottom: 0; } }

.container {
	// width: 50%
	margin: 0 auto; }


.header {
	width: 100%;
	height: 300px;
	@include r(600) {
		height: 400px; } }



.main {

	position: relative;
	top: -250px;
	padding: 0 20px;
	margin-bottom: -200px;
	@include r(600) {
		background: rgba(0, 0, 0, .08); }

	@include r(900) {
		top: -400px;
		margin-bottom: -400px; }
	&__image {
		position: relative;
		display: block;
		font-size: 120px;
		text-align: center;
		margin: 0 auto;
		margin-top: 50px;
		@include r(600) {
			margin-top: 20px; }



		.icon {
			fill: rgba(255, 255, 255, .85); } }
	&__title {
		text-align: center;
		font-size:  3rem;
		background: rgba(255, 255, 255, .38);
		font-family: 'Open Sans', sans-serif;
		margin-bottom: 30px;
		color: #fff;
		margin: 0 auto;
		padding: 5px 15px;
		box-shadow: 0 0 5px #000;
		z-index: 1;
		@include r(600) {
			margin-top: -5%;
			margin-bottom: 5%; } }

	&__suptitle {
		font-size: 1.2rem;
		padding-bottom: 1rem;
		margin-top: 200px;
		text-align: center;
		margin-bottom: 14px; }
	&__candles {
		display: flex;
		justify-content: space-around;
		align-items: center;
		box-shadow: 0 0 5px #000;
		margin-bottom: 50px;
		position: relative;
		@include r(600) {
			flex-direction: column;
			padding-bottom: 60px;
			.icon {
				// margin-top: 20px
				font-size: 100px; } }


		background: url('../img/candle-bg.jpg') 0% 100% no-repeat;
		// background: rgba(0, 0, 0, .1)
		background-size: cover;
		animation-name: candles;
		animation-duration: 85s;
		animation-iteration-count: 10;
		height: 50vh;
		overflow: hidden;
		&:before {
			content: '';
			position: absolute;
			width: 100%;
			height: 100%;
			background: rgba(0, 0, 0, .58);
			margin-bottom: -60px;
			margin-top: -60px;
			@include r(600) {
				margin-bottom: -120px; } } } }



@keyframes candles {

	from {
		background-position: 100% 100%;
		// margin: 0
		// left: 0
		// top: 0
		// opacity: 1
 }		// height: 40vh
	to {
		background-position: 0% 100%;
		// margin: 50px
		// opacity: 0
 } }		// height: 60vh

.btns {

	text-align: center;
	padding-bottom: 40px;
	position: relative;
	&:before {
		content: '';
		left: 50%;
		transform: translate(-50%, 0);
		width: 45%;
		height: 5px;
		background: rgba(168, 246, 56, .2);
		bottom: 10%;
		position: absolute; } }


.btn {
	display: inline-block;
	font-size: 14px;
	font-weight: bold;
	line-height: 43px;
	padding: 0 10px;
	border-radius: 5px;
	width: 130px;
	margin-right: 7px;
	margin-bottom: 40px;
	color: #FFF;
	&:last-of-type {
		margin-right: 0; } }
.btn-pink {
	background: #ff6484; }
.btn-blue {
	background: #37a2eb; }
.btn-black {
	background: #343639; }

.chart {
	background: rgba(168, 246, 56, .081);
	padding-bottom: 40px;
	// margin-top: 50px


	.container {
		padding-left: 35px;
		padding-right: 25px;
		display: flex;
		align-items: center;

		@include r(600) {
			flex-direction: column; } }


	&__image {
		width: 	90%;
		min-height: 0;
		padding-top: 46.5%;
		margin-top: 30px;
		margin-bottom: 30px;
		// margin-top: 30px
		@include r(600) {
 } }			// background-size: cover -- pzkrykro co nie pracuje

	&__left {
		width: 100%;
		@include r(540) {
			flex: none; } }
	&__top {
		display: flex;
		margin-bottom: 20px;
		align-items: baseline;
		@include r(600) {
			justify-content: center;
			flex-direction: column;
			align-items: center; } }


	&__new {
		margin-bottom: 30px;
		margin-right: 15px;
		font-size: .8rem;
		position: relative;
		top: -.2rem;
		line-height: 1.1rem;
		padding: 5px 15px;
		border-radius: 7px;
		background: #4bc0c0;
		color: #fff;
		font-weight: bold;
		@include r(600) {
			top: -.6rem; } }


	&__title {
		font-size: 1.2rem;
		font-weight: bold; }
	&__text {

		font-size: 1rem;
		line-height: 1.5;
		@include r(600) {
			margin-bottom: 30px; } } }

