html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-chart {
  width: 1em;
  height: 1em;
  fill: #FFF;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

body {
  background: #FFF;
  font-size: 50px;
  font-size: calc(16px + (50 - 16) * ((100vw - 600px) / (1000 - 600)));
  line-height: 1.4;
}

@media (max-width: 600px) {
  body {
    font-size: 16px;
  }
}

@media (min-width: 1000px) {
  body {
    font-size: 50px;
  }
}

.block {
  display: flex;
}

* {
  font-family: 'Open Sans', sans-serif;
}

.wrap {
  width: 50%;
  margin: 0 auto;
  border: 1px solid #bbb;
  margin-bottom: 40px;
}

@media only screen and (max-width: 900px) {
  .wrap {
    width: 100%;
    border: none;
    margin-bottom: 0;
  }
}

.container {
  margin: 0 auto;
}

.header {
  width: 100%;
  height: 300px;
}

@media only screen and (max-width: 600px) {
  .header {
    height: 400px;
  }
}

.main {
  position: relative;
  top: -250px;
  padding: 0 20px;
  margin-bottom: -200px;
}

@media only screen and (max-width: 600px) {
  .main {
    background: rgba(0, 0, 0, 0.08);
  }
}

@media only screen and (max-width: 900px) {
  .main {
    top: -400px;
    margin-bottom: -400px;
  }
}

.main__image {
  position: relative;
  display: block;
  font-size: 120px;
  text-align: center;
  margin: 0 auto;
  margin-top: 50px;
}

@media only screen and (max-width: 600px) {
  .main__image {
    margin-top: 20px;
  }
}

.main__image .icon {
  fill: rgba(255, 255, 255, 0.85);
}

.main__title {
  text-align: center;
  font-size: 3rem;
  background: rgba(255, 255, 255, 0.38);
  font-family: 'Open Sans', sans-serif;
  margin-bottom: 30px;
  color: #fff;
  margin: 0 auto;
  padding: 5px 15px;
  box-shadow: 0 0 5px #000;
  z-index: 1;
}

@media only screen and (max-width: 600px) {
  .main__title {
    margin-top: -5%;
    margin-bottom: 5%;
  }
}

.main__suptitle {
  font-size: 1.2rem;
  padding-bottom: 1rem;
  margin-top: 200px;
  text-align: center;
  margin-bottom: 14px;
}

.main__candles {
  display: flex;
  justify-content: space-around;
  align-items: center;
  box-shadow: 0 0 5px #000;
  margin-bottom: 50px;
  position: relative;
  background: url("../img/candle-bg.jpg") 0% 100% no-repeat;
  background-size: cover;
  animation-name: candles;
  animation-duration: 85s;
  animation-iteration-count: 10;
  height: 50vh;
  overflow: hidden;
}

@media only screen and (max-width: 600px) {
  .main__candles {
    flex-direction: column;
    padding-bottom: 60px;
  }
  .main__candles .icon {
    font-size: 100px;
  }
}

.main__candles:before {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.58);
  margin-bottom: -60px;
  margin-top: -60px;
}

@media only screen and (max-width: 600px) {
  .main__candles:before {
    margin-bottom: -120px;
  }
}

@keyframes candles {
  from {
    background-position: 100% 100%;
  }
  to {
    background-position: 0% 100%;
  }
}

.btns {
  text-align: center;
  padding-bottom: 40px;
  position: relative;
}

.btns:before {
  content: '';
  left: 50%;
  transform: translate(-50%, 0);
  width: 45%;
  height: 5px;
  background: rgba(168, 246, 56, 0.2);
  bottom: 10%;
  position: absolute;
}

.btn {
  display: inline-block;
  font-size: 14px;
  font-weight: bold;
  line-height: 43px;
  padding: 0 10px;
  border-radius: 5px;
  width: 130px;
  margin-right: 7px;
  margin-bottom: 40px;
  color: #FFF;
}

.btn:last-of-type {
  margin-right: 0;
}

.btn-pink {
  background: #ff6484;
}

.btn-blue {
  background: #37a2eb;
}

.btn-black {
  background: #343639;
}

.chart {
  background: rgba(168, 246, 56, 0.081);
  padding-bottom: 40px;
}

.chart .container {
  padding-left: 35px;
  padding-right: 25px;
  display: flex;
  align-items: center;
}

@media only screen and (max-width: 600px) {
  .chart .container {
    flex-direction: column;
  }
}

.chart__image {
  width: 90%;
  min-height: 0;
  padding-top: 46.5%;
  margin-top: 30px;
  margin-bottom: 30px;
}

.chart__left {
  width: 100%;
}

@media only screen and (max-width: 540px) {
  .chart__left {
    flex: none;
  }
}

.chart__top {
  display: flex;
  margin-bottom: 20px;
  align-items: baseline;
}

@media only screen and (max-width: 600px) {
  .chart__top {
    justify-content: center;
    flex-direction: column;
    align-items: center;
  }
}

.chart__new {
  margin-bottom: 30px;
  margin-right: 15px;
  font-size: .8rem;
  position: relative;
  top: -.2rem;
  line-height: 1.1rem;
  padding: 5px 15px;
  border-radius: 7px;
  background: #4bc0c0;
  color: #fff;
  font-weight: bold;
}

@media only screen and (max-width: 600px) {
  .chart__new {
    top: -.6rem;
  }
}

.chart__title {
  font-size: 1.2rem;
  font-weight: bold;
}

.chart__text {
  font-size: 1rem;
  line-height: 1.5;
}

@media only screen and (max-width: 600px) {
  .chart__text {
    margin-bottom: 30px;
  }
}
